import rawObject from 'raw-object';

export function clock(interval, {
    tick = null,
    sync = 1000,
    stop = ()=>{},
    pause = ()=>{},
    showDiff = false,
} = {}){

    let running = false,
        paused = false,
        timeoutID = null,
        pausePassed = false,
        stopOn = null,
        startTime,
        count = 0,
        base = null;

    if(typeof tick !== 'function'){
        throw new Error(`${tick} is not a function`);
    }

    const timer = rawObject({
        stop(){
            running = false;
            if(timeoutID !== null){
                clearTimeout(timeoutID);
                stop.call(this, base);
            }
            return this;
        },
        pause(){
            paused = true;
            pause.call(this, base);
            return this;
        },
        start(times){
            stopOn = isNaN(times) ? stopOn : times;
            count = 0;

            if(running) return;
            if(paused){
                paused = false; return;
            }

            paused = false;
            running = true;
            base = startTime = Date.now();

            if(sync){
                //Synchronize start interval
                //to rounded interval
                base = startTime = startTime - (startTime % sync) + sync;

                timeoutID = setTimeout(next, startTime - Date.now());
                return this;
            }

            next();
            return this;
        }
    });

    function next(){
        if(!running) return;

        //Stabilize the timer interval
        base += interval;
        let time = Date.now();
        let diff = time - base;
        let frame = interval - diff;

        timeoutID = setTimeout(
            next, frame < 0 ? 0 : frame);

        if(paused) return;

        ++count;

        if(stopOn && count >= stopOn){
            return timer.stop();
        }

        tick.call(timer, base, time);

    }

    return timer;
}
